import { Action, State, StateContext } from "@ngxs/store";
import { Injectable } from "@angular/core";
import {
  LightThemeAction,
  DarkThemeAction,
  ToggleThemeAction,
} from "./theme.actions";

export class ThemeStateModel {
  public dark = true;
}

@State<ThemeStateModel>({
  name: "theme",
  defaults: {
    dark: false,
  },
})
@Injectable()
export class ThemeState {
  @Action(LightThemeAction)
  public lightThemeAction(ctx: StateContext<ThemeStateModel>): void {
    ctx.setState({ dark: false });
  }

  @Action(DarkThemeAction)
  public darkThemeAction(ctx: StateContext<ThemeStateModel>): void {
    ctx.setState({ dark: true });
  }

  @Action(ToggleThemeAction)
  public toggleThemeAction(ctx: StateContext<ThemeStateModel>): void {
    const state = ctx.getState();
    ctx.setState({ dark: !state.dark });
  }
}
