import { Injectable } from "@angular/core";
import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
} from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";
import { Store } from "@ngxs/store";
import { AuthState } from "src/app/store/auth/auth.state";
import { AuthService } from "../services/auth/auth.service";
import { TokenResult } from "../services/auth/auth.input";
import { catchError, switchMap, take } from "rxjs/operators";
import { LoginAction } from "src/app/store/auth/auth.actions";
import { throwError } from "rxjs";
import { Router } from "@angular/router";

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  constructor(
    private store: Store,
    private authService: AuthService,
    private router: Router
  ) {}

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    if (!this.isApiUrl(req.url)) {
      return next.handle(req);
    }
    // check token
    if (this.isExpired(this.store.selectSnapshot(AuthState.expireAt))) {
      return this.refreshToken()
        .pipe(take(1))
        .pipe(
          switchMap((token) => {
            this.store.dispatch(new LoginAction({ token }));
            return next.handle(this.addTokenToRequest(req));
          })
        )
        .pipe(
          catchError((err: any) => {
            this.router.navigate(["/auth/login"]);
            return throwError(err);
          })
        );
    }
    return next.handle(this.addTokenToRequest(req));
  }

  private addTokenToRequest(req: HttpRequest<any>) {
    const headers = {
      // eslint-disable-next-line @typescript-eslint/naming-convention
      Authorization: "",
    };
    const accessToken = this.store.selectSnapshot(AuthState.accessToken);
    if (accessToken) {
      headers.Authorization = `Bearer ${accessToken}`;
    }
    return req.clone({
      setHeaders: headers,
    });
  }

  /**
   * Check if requested link is API
   */
  private isApiUrl(url: string): boolean {
    const isTokenUrl: boolean = url.endsWith("/token");
    return !isTokenUrl;
  }

  /**
   * Refresh token
   */
  private refreshToken(): Observable<TokenResult> {
    const refreshToken = this.store.selectSnapshot(AuthState.refreshToken);
    return this.authService.token({
      grantType: "refresh_token",
      responseType: "token id_token",
      refreshToken,
    });
  }

  /**
   * Check token if expired
   */
  private isExpired(expireAt: Date | undefined): boolean {
    if (!expireAt) {
      return true;
    }
    expireAt = typeof expireAt === "string" ? new Date(expireAt) : expireAt;
    const now = new Date();
    return (expireAt.getTime() - now.getTime()) / 1000 < 10;
  }
}
