import { Action, Selector, State, StateContext } from "@ngxs/store";
import {
  ShowSidebarAction,
  HideSidebarAction,
  ToggleSidebarAction,
} from "./sidebar.actions";
import { Injectable } from "@angular/core";

export class SidebarStateModel {
  public status = true;
}

@State<SidebarStateModel>({
  name: "sidebar",
  defaults: {
    status: true,
  },
})
@Injectable()
export class SidebarState {
  @Selector()
  public static status(state: SidebarStateModel): boolean {
    return state.status;
  }

  @Action(ShowSidebarAction)
  public showSidebarAction(ctx: StateContext<SidebarStateModel>): void {
    ctx.setState({ status: true });
  }

  @Action(HideSidebarAction)
  public hideSidebarAction(ctx: StateContext<SidebarStateModel>): void {
    ctx.setState({ status: false });
  }

  @Action(ToggleSidebarAction)
  public toggleSidebarAction(ctx: StateContext<SidebarStateModel>): void {
    const state = ctx.getState();
    ctx.setState({ status: !state.status });
  }
}
