import { BrowserModule } from "@angular/platform-browser";
import { LOCALE_ID, NgModule } from "@angular/core";
import { AppComponent } from "./app.component";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { ReactiveFormsModule } from "@angular/forms";
import { AppRoutingModule } from "./app.routing.module";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { NgxsModule } from "@ngxs/store";
import { NgxsStoragePluginModule } from "@ngxs/storage-plugin";
import { LoaderState } from "./store/loader/loader.state";
import { SidebarState } from "./store/sidebar/sidebar.state";
import { HeaderState } from "./store/header/header.state";
import { ThemeState } from "./store/theme/theme.state";
import { AuthState } from "./store/auth/auth.state";
import { environment } from "src/environments/environment";
import { AuthService } from "./shared/services/auth/auth.service";
import { BaseInterceptor } from "src/app/shared/interceptors/base.interceptor";
import { MatProgressBarModule } from "@angular/material/progress-bar";
import { AuthInterceptor } from "./shared/interceptors/auth.interceptor";
import { MAT_DATE_FORMATS, MAT_DATE_LOCALE } from "@angular/material/core";
import { APP_BASE_HREF } from "@angular/common";

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    ReactiveFormsModule,
    HttpClientModule,
    MatProgressBarModule,
    AppRoutingModule,
    NgxsModule.forRoot(
      [LoaderState, SidebarState, AuthState, HeaderState, ThemeState],
      { developmentMode: !environment.production }
    ),
    NgxsStoragePluginModule.forRoot(),
  ],
  providers: [
    AuthService,
    { provide: APP_BASE_HREF, useValue: "/" },
    { provide: LOCALE_ID, useValue: "ar-SA" },
    { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: BaseInterceptor, multi: true },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
