import { Injectable } from "@angular/core";
import { State, Action, StateContext, Selector } from "@ngxs/store";
import { LoginAction, LogoutAction } from "./auth.actions";

export interface AuthStateModel {
  username?: string;
  name?: string;
  accessToken?: string;
  expireAt?: Date;
  refreshToken?: string;
}

@State<AuthStateModel>({
  name: "auth",
})
@Injectable()
export class AuthState {
  /**
   * Constructor
   */
  constructor() {}

  @Selector()
  static isAuthenticated(state: AuthStateModel): boolean {
    return state.accessToken !== undefined;
  }

  @Selector()
  static accessToken(state: AuthStateModel): string | undefined {
    return state.accessToken;
  }

  @Selector()
  static refreshToken(state: AuthStateModel): string | undefined {
    return state.refreshToken;
  }

  @Selector()
  static expireAt(state: AuthStateModel): Date | undefined {
    return state.expireAt;
  }

  @Action(LoginAction)
  login(
    { patchState }: StateContext<AuthStateModel>,
    { payload: { token } }: LoginAction
  ): void {
    const expireAt = new Date();
    expireAt.setSeconds(expireAt.getSeconds() + token.expiresIn);
    patchState({
      accessToken: token.accessToken,
      expireAt,
      refreshToken: token.refreshToken,
    });
  }

  @Action(LogoutAction)
  logout({ setState }: StateContext<AuthStateModel>): void {
    setState({
      accessToken: undefined,
    });
  }
}
