import { Action, Selector, State, StateContext } from "@ngxs/store";
import { HeaderUpdateAction } from "./header.actions";
import { Injectable } from "@angular/core";

export class HeaderStateModel {
  public title = "Dashboard";
}

@State<HeaderStateModel>({
  name: "header",
  defaults: {
    title: "Dashboard",
  },
})
@Injectable()
export class HeaderState {
  @Selector()
  public static title(state: HeaderStateModel): string {
    return state.title;
  }

  @Action(HeaderUpdateAction)
  public headerUpdateAction(
    ctx: StateContext<HeaderStateModel>,
    { payload: { title } }: HeaderUpdateAction
  ): void {
    if (title === undefined) {
      return;
    }
    ctx.setState({ title });
  }
}
