import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";
import { TokenInput, TokenResult } from "./auth.input";

/**
 * Authentication Service
 * It provides methods to login, logout
 */
@Injectable()
export class AuthService {
  path = "";

  constructor(private http: HttpClient) {}

  /**
   * Get token
   */
  token(input: TokenInput): Observable<TokenResult> {
    return this.http.post<TokenResult>(
      `${this.path}resource-owner/token`,
      input
    );
  }

  /**
   * Logout
   */
  logout(): Observable<void> {
    const httpOptions = {
      withCredentials: true,
    };
    return this.http.put<void>(`${this.path}/logout`, {}, httpOptions);
  }
}
